import React from "react";
import emblem_challenger from "../../../../../images/emblems/Challenger.png";
import emblem_grandmaster from "../../../../../images/emblems/Grandmaster.png";
import emblem_master from "../../../../../images/emblems/Master.png";
import emblem_diamond from "../../../../../images/emblems/Diamond.png";
import emblem_platinum from "../../../../../images/emblems/Platinum.png";
import emblem_gold from "../../../../../images/emblems/Gold.png";
import emblem_silver from "../../../../../images/emblems/Silver.png";
import emblem_bronze from "../../../../../images/emblems/Bronze.png";
import emblem_iron from "../../../../../images/emblems/Iron.png";
import getTierNumberToString from "../../../../../../../../tools/generator/rank";

export enum type {
  solo,
  flex,
}

interface Props {
  type: type;
  rank: number | null | undefined;
  tier: number | null | undefined;
  point: number | null | undefined;

  win: number | null | undefined;
  loss: number | null | undefined;
}

function Rank(props: Props) {
  var rank_type = props.type ? "자유" : "솔로";
  var emblem = emblem_iron;

  switch (props.tier) {
    case 0:
      emblem = emblem_iron;
      break;
    case 1:
      emblem = emblem_bronze;
      break;
    case 2:
      emblem = emblem_silver;
      break;
    case 3:
      emblem = emblem_gold;
      break;
    case 4:
      emblem = emblem_platinum;
      break;
    case 5:
      emblem = emblem_diamond;
      break;
    case 6:
      emblem = emblem_master;
      break;
    case 7:
      emblem = emblem_grandmaster;
      break;
    case 8:
      emblem = emblem_challenger;
      break;
  }

  return (
    <div className="flex">
      <img className="w-[195px]" src={emblem} alt="Tier" />
      <div className="flex flex-col gap-2 justify-center ml-4">
        <div className="w-64">
          <div className="text-[#666666] text-sm">{rank_type} 랭크</div>
          <div className="flex flex-row gap-2 text-[#3d3d3d] font-extrabold text-2xl">
            <div>
              {getTierNumberToString(
                props.tier as number,
                props.rank as number
              )}
            </div>
            <div>{props.point} LP</div>
          </div>
        </div>
        <div className="flex flex-row gap-1 text-[#8f8f8f] font-bold text-xs">
          <div>승률</div>
          <div>
            {(
              ((props.win || 0) / ((props.win || 0) + (props.loss || 0))) *
              100
            ).toFixed(2)}
            %
          </div>
          <div>
            ({props.win}승 {props.loss}패)
          </div>
        </div>
      </div>
    </div>
  );
}

export default Rank;
